import { Slot } from "@radix-ui/react-slot";
import { clsx } from "clsx";
import { ButtonHTMLAttributes, ReactNode } from "react";
export interface ButtonProps extends ButtonHTMLAttributes<HTMLButtonElement> {
  children: ReactNode;
  asChild?: boolean;
  className?: string;
}

export const Button = ({
  children,
  asChild,
  className,
  ...props
}: ButtonProps) => {
  const Comp = asChild ? Slot : "button";

  return (
    <Comp
      className={clsx(
        "py-3 px-4 bg-cyan-500 hover:bg-cyan-300  transition-colors focus:ring-2 ring-cyan-500 rounded font-semibold text-black-900 text-sm w-full",
        className
      )}
      {...props}
    >
      {children}
    </Comp>
  );
};
